@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
}

.header {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
}

.nav__list {
  flex-direction: row;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
  transition: color .3s ease-in-out;
}

.nav__item {
  position: relative;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-inline: 3px;
  display: block;
}

.card__head {
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__text {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__rating-text {
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-number {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  transition: all .3s ease-in-out;
}

.card:hover {
  transition: transform .3s ease-in-out;
  transform: scale(1.2);
}

.card:hover .card__head {
  color: #34568b;
  transition: color .3s ease-in-out;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 > .stars__star:nth-child(-n+1), .stars--2 > .stars__star:nth-child(-n+2), .stars--3 > .stars__star:nth-child(-n+3), .stars--4 > .stars__star:nth-child(-n+4), .stars--5 > .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  place-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.logo__img {
  display: block;
}
